"use client"

import { useState, useEffect, useCallback } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Progress } from "@/components/ui/progress" // Progress 컴포넌트 추가
import {
  Calendar,
  Clock,
  DollarSign,
  Plus,
  MessageSquare,
  Settings,
  LogOut,
  Sparkles,
  RefreshCw,
  Trash2,
  Loader2,
  ShieldCheck,
  Video,
  ChevronRight,
  TrendingUp,
  UserCheck,
  Wallet,
  Target,
  ArrowRightLeft
} from "lucide-react"
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogFooter
} from "@/components/ui/dialog"

// --- 인터페이스 ---
interface ConsultationProduct {
  id: string;
  title: string;
  duration: string;
  price: string;
  description: string;
}

interface Reservation {
  id: string;
  userName: string;
  date: string;
  time: string;
  status: "PAID" | "COMPLETED";
}

export default function AdvisorDashboardPage() {
  const router = useRouter()
  const [userName, setUserName] = useState("")
  const [isLoading, setIsLoading] = useState(true)
  
  const [products, setProducts] = useState<ConsultationProduct[]>([
    { id: "1", title: "심층 재무 설계 상담", duration: "50분", price: "50,000", description: "개인별 맞춤 자산 포트폴리오를 구성해드립니다." }
  ])
  const [reservations, setReservations] = useState<Reservation[]>([
    { id: "R1", userName: "김철수", date: "2025-12-20", time: "14:00", status: "PAID" },
    { id: "R2", userName: "이영희", date: "2025-12-21", time: "10:00", status: "PAID" }
  ])

  const [isAddProductOpen, setIsAddProductOpen] = useState(false)
  const [newProduct, setNewProduct] = useState({ title: "", duration: "", price: "", description: "" })

  const fetchData = useCallback(async () => {
    setIsLoading(true)
    const storedName = localStorage.getItem("name") || "전문가"
    setUserName(storedName)
    setTimeout(() => setIsLoading(false), 800)
  }, [])

  useEffect(() => {
    fetchData()
  }, [fetchData])

  const handleLogout = () => {
    if (confirm("로그아웃 하시겠습니까?")) {
      localStorage.clear()
      router.push("/")
    }
  }

  const handleAddProduct = () => {
    const product: ConsultationProduct = {
      id: Math.random().toString(),
      ...newProduct
    }
    setProducts([product, ...products])
    setIsAddProductOpen(false)
    setNewProduct({ title: "", duration: "", price: "", description: "" })
  }

  if (isLoading) {
    return (
      <div className="min-h-screen flex flex-col items-center justify-center bg-background">
        <Loader2 className="h-10 w-10 animate-spin text-primary mb-4" />
        <p className="text-sm text-muted-foreground animate-pulse">전문가 데이터를 불러오고 있습니다...</p>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-background">
      <header className="border-b border-border bg-card/50 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <Link href="/" className="flex items-center gap-2">
            <Image src="/logo.png" alt="Next Me" width={120} height={32} className="h-8 w-auto" priority />
            <Badge variant="outline" className="ml-2 border-primary text-primary font-bold">ADVISOR</Badge>
          </Link>
          <div className="flex items-center gap-4">
            <Link href="/dashboard/chat"><Button variant="ghost" size="icon"><MessageSquare className="h-5 w-5" /></Button></Link>
            <Link href="/advisor/settings"><Button variant="ghost" size="icon"><Settings className="h-5 w-5" /></Button></Link>
            <Button variant="ghost" size="icon" onClick={handleLogout} title="로그아웃">
              <LogOut className="h-5 w-5 text-muted-foreground hover:text-destructive transition-colors" />
            </Button>
            <Avatar className="border"><AvatarFallback className="bg-primary/5">{userName.charAt(0)}</AvatarFallback></Avatar>
          </div>
        </div>
      </header>

      <div className="container mx-auto px-4 py-8">
        <div className="flex justify-between items-end mb-8">
          <div>
            <h1 className="text-3xl font-bold mb-2">반갑습니다, {userName} 전문가님</h1>
            <p className="text-muted-foreground">본인의 자산 현황과 상담 일정을 한눈에 관리하세요.</p>
          </div>
          <div className="flex gap-2">
            <Button variant="outline" size="sm" onClick={() => router.push('/advisor/accounts')}>
              <Wallet className="h-4 w-4 mr-2" /> 계좌 연동
            </Button>
            <Button size="sm" onClick={() => setIsAddProductOpen(true)}>
              <Plus className="h-4 w-4 mr-2" /> 상품 등록
            </Button>
          </div>
        </div>

        {/* TOP STATS: 자산 및 수익 정보 추가 */}
        <div className="grid md:grid-cols-4 gap-6 mb-8">
          <Card className="p-6">
            <div className="flex items-center justify-between mb-4">
              <div className="h-10 w-10 rounded-lg bg-blue-500/10 flex items-center justify-center"><Wallet className="h-5 w-5 text-blue-500" /></div>
              <Badge variant="outline" className="text-[10px]">연동 4건</Badge>
            </div>
            <p className="text-sm text-muted-foreground mb-1">총 관리 자산</p>
            <p className="text-2xl font-bold">₩24,580,000</p>
          </Card>
          <Card className="p-6">
            <div className="flex items-center justify-between mb-4">
              <div className="h-10 w-10 rounded-lg bg-primary/10 flex items-center justify-center"><DollarSign className="h-5 w-5 text-primary" /></div>
              <TrendingUp className="h-4 w-4 text-green-500" />
            </div>
            <p className="text-sm text-muted-foreground mb-1">이번 달 정산 예정액</p>
            <p className="text-2xl font-bold">₩1,450,000</p>
          </Card>
          <Card className="p-6">
            <div className="flex items-center justify-between mb-4">
              <div className="h-10 w-10 rounded-lg bg-accent/10 flex items-center justify-center"><UserCheck className="h-5 w-5 text-accent" /></div>
            </div>
            <p className="text-sm text-muted-foreground mb-1">상담 진행 건수</p>
            <p className="text-2xl font-bold">128건</p>
          </Card>
          <Card className="p-6">
            <div className="flex items-center justify-between mb-4">
              <div className="h-10 w-10 rounded-lg bg-destructive/10 flex items-center justify-center"><Video className="h-5 w-5 text-destructive" /></div>
            </div>
            <p className="text-sm text-muted-foreground mb-1">평균 만족도</p>
            <p className="text-2xl font-bold">4.9/5.0</p>
          </Card>
        </div>

        <div className="grid lg:grid-cols-3 gap-6">
          <div className="lg:col-span-2 space-y-6">
            {/* 예약 내역 리스트 */}
            <Card className="p-6 border-primary/20 shadow-sm">
              <h2 className="text-xl font-semibold mb-4 flex items-center gap-2">
                <Calendar className="h-5 w-5 text-primary" /> 결제 완료된 상담 예약
              </h2>
              <div className="grid gap-3">
                {reservations.map((res, index) => (
                  <div key={index} className="flex items-center justify-between p-4 rounded-lg bg-secondary/20 border border-border/50 hover:bg-secondary/40 transition-all group">
                    <div className="flex items-center gap-4">
                      <div className="w-10 h-10 rounded-full bg-white border flex items-center justify-center shadow-sm font-bold text-primary">
                        {res.userName.charAt(0)}
                      </div>
                      <div>
                        <p className="font-bold text-sm text-foreground">{res.userName} 님</p>
                        <p className="text-xs text-muted-foreground">{res.date} · {res.time} 상담 예정</p>
                      </div>
                    </div>
                    <div className="flex items-center gap-2">
                      <Badge className="bg-green-500/10 text-green-600 border-green-200">결제완료</Badge>
                      <Button size="sm" variant="outline" className="h-8">입장하기</Button>
                    </div>
                  </div>
                ))}
              </div>
            </Card>

            {/* 최근 거래 내역 섹션 추가 */}
            <Card className="p-6">
              <h2 className="text-xl font-semibold mb-4 flex items-center gap-2">
                <ArrowRightLeft className="h-5 w-5 text-primary" /> 최근 입출금 내역
              </h2>
              <div className="space-y-4">
                {[
                  { title: "상담료 입금 (김철수)", amount: "+50,000", date: "2025-12-18", type: "plus" },
                  { title: "데이터 플랫폼 정기결제", amount: "-19,000", date: "2025-12-17", type: "minus" },
                  { title: "상담료 입금 (이영희)", amount: "+50,000", date: "2025-12-16", type: "plus" }
                ].map((item, i) => (
                  <div key={i} className="flex items-center justify-between py-2 border-b last:border-0 border-border/50">
                    <div>
                      <p className="text-sm font-medium">{item.title}</p>
                      <p className="text-xs text-muted-foreground">{item.date}</p>
                    </div>
                    <p className={`text-sm font-bold ${item.type === 'plus' ? 'text-blue-500' : 'text-foreground'}`}>
                      {item.amount}
                    </p>
                  </div>
                ))}
              </div>
            </Card>

            {/* 상담 상품 관리 섹션 */}
            <Card className="p-6 relative overflow-hidden">
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-xl font-semibold">운영 중인 상담 상품</h2>
                <Button variant="ghost" size="sm" onClick={() => setIsAddProductOpen(true)} className="text-primary font-bold">+ 추가</Button>
              </div>
              <div className="grid md:grid-cols-2 gap-4">
                {products.map((product) => (
                  <div key={product.id} className="p-4 rounded-xl border bg-card hover:shadow-md transition-shadow relative group">
                    <div className="flex justify-between items-start mb-2">
                      <h3 className="font-bold text-lg">{product.title}</h3>
                      <Badge variant="secondary">{product.duration}</Badge>
                    </div>
                    <p className="text-sm text-muted-foreground mb-4 line-clamp-2">{product.description}</p>
                    <div className="flex justify-between items-center mt-auto">
                      <p className="font-bold text-primary">₩{product.price}</p>
                      <Button variant="ghost" size="icon" className="h-8 w-8 text-muted-foreground hover:text-destructive group-hover:opacity-100 opacity-0 transition-opacity">
                        <Trash2 className="h-4 w-4" />
                      </Button>
                    </div>
                  </div>
                ))}
              </div>
            </Card>
          </div>

          <div className="space-y-6">
            {/* 목표 설정 섹션 추가 */}
            <Card className="p-6">
              <h2 className="text-lg font-semibold mb-4 flex items-center gap-2">
                <Target className="h-5 w-5 text-primary" /> 나의 목표 현황
              </h2>
              <div className="space-y-6">
                <div>
                  <div className="flex justify-between text-xs mb-2">
                    <span className="font-medium text-muted-foreground">이번 달 목표 상담 (150건)</span>
                    <span className="font-bold">85%</span>
                  </div>
                  <Progress value={85} className="h-2" />
                </div>
                <div>
                  <div className="flex justify-between text-xs mb-2">
                    <span className="font-medium text-muted-foreground">자산 목표 (5,000만원)</span>
                    <span className="font-bold">49%</span>
                  </div>
                  <Progress value={49} className="h-2" />
                </div>
                <Button variant="outline" className="w-full text-xs h-9">목표 수정하기</Button>
              </div>
            </Card>

            <Card className="p-6">
              <h2 className="text-lg font-semibold mb-4">전문가 퀵 메뉴</h2>
              <div className="space-y-3">
                <Button variant="outline" className="w-full justify-start bg-transparent py-6 text-sm" size="lg" onClick={() => router.push("/advisor/profile")}>
                  <ShieldCheck className="h-5 w-5 mr-3 text-primary" /> 전문가 프로필 수정
                </Button>
                <Button variant="outline" className="w-full justify-start bg-transparent py-6 text-sm" size="lg" onClick={() => router.push("/advisor/settlement")}>
                  <RefreshCw className="h-5 w-5 mr-3 text-primary" /> 정산 내역 확인
                </Button>
                <Button variant="outline" className="w-full justify-start bg-transparent py-6 text-sm" size="lg" onClick={() => router.push("/advisor/reviews")}>
                  <Sparkles className="h-5 w-5 mr-3 text-primary" /> 리뷰 관리
                </Button>
              </div>
            </Card>

            <Card className="p-6 bg-gradient-to-br from-primary/5 to-accent/5 border-primary/10 relative overflow-hidden">
               <h2 className="text-lg font-semibold mb-2 flex items-center gap-2"><Sparkles className="h-4 w-4 text-primary"/>전문가 팁</h2>
               <p className="text-sm text-muted-foreground mb-4 leading-relaxed italic">
                 "상담 전 유저의 거래 내역 데이터를 미리 분석하면 상담 만족도가 평균 40% 향상됩니다."
               </p>
               <Button variant="link" size="sm" className="p-0 text-primary h-auto font-bold">더 많은 노하우 보기 →</Button>
            </Card>
          </div>
        </div>
      </div>

      <Dialog open={isAddProductOpen} onOpenChange={setIsAddProductOpen}>
        <DialogContent className="sm:max-w-[425px]">
          <DialogHeader>
            <DialogTitle className="font-bold">상담 상ㅇ품 등록</DialogTitle>
          </DialogHeader>
          <div className="grid gap-4 py-4">
            <div className="space-y-2">
              <Label htmlFor="title" className="text-xs font-bold">상담명</Label>
              <Input id="title" placeholder="예: 1:1 재무 컨설팅" value={newProduct.title} onChange={(e)=>setNewProduct({...newProduct, title: e.target.value})} />
            </div>
            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="duration" className="text-xs font-bold">상담 시간</Label>
                <Input id="duration" placeholder="50분" value={newProduct.duration} onChange={(e)=>setNewProduct({...newProduct, duration: e.target.value})} />
              </div>
              <div className="space-y-2">
                <Label htmlFor="price" className="text-xs font-bold">가격 (₩)</Label>
                <Input id="price" type="text" placeholder="50,000" value={newProduct.price} onChange={(e)=>setNewProduct({...newProduct, price: e.target.value})} />
              </div>
            </div>
            <div className="space-y-2">
              <Label htmlFor="description" className="text-xs font-bold">상담 상세 설명</Label>
              <Textarea id="description" placeholder="상담에 대해 설명해주세요." value={newProduct.description} onChange={(e)=>setNewProduct({...newProduct, description: e.target.value})} />
            </div>
          </div>
          <DialogFooter>
            <Button onClick={handleAddProduct} className="w-full font-bold">등록 완료</Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  )
}